::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}

::-webkit-scrollbar-track {
  background-color: #000000;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb {
  background-color: #F58F7C;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}

::-webkit-scrollbar-thumb:active {
  background-color: #777;
}

::-webkit-scrollbar-button {
  display: none;
}

::-webkit-scrollbar-corner {
  background-color: transparent;
}

::-webkit-scrollbar-progress-piece {
  background-color: #F58F7C;
  border-radius: 5px;
}

::-webkit-scrollbar-progress-piece:start {
  margin: 0 0 10px 0;
}

::-webkit-scrollbar-progress-piece:end {
  margin: 10px 0 0 0;
}
.card-body{
  min-height: "wrap-content";
  height: "wrap-content";
}
.card-title{
    font-weight: 700;
}
// .row {
